/**
 *  vue-ls
 */
import Vue from 'vue';
import Storage from 'vue-ls';

import * as _ from 'lodash';

// local storage 
Vue.use(_.clone(Storage), {
  namespace: 'vuealain_', // sorage 
  name: 'ls', // vuethis.$ls
  storage: 'local',
});

Vue.use(_.clone(Storage), {
  namespace: 'vuealain_ss_', // sorage 
  name: 'ss', // vuethis.$ss
  storage: 'session',
});
